import React from 'react';
import Tittles from '../Components/Tittles';
import pdf from '../pdf/cert.pdf';


function Patent(){
    return(
        <div className="Patent">
            {/* <h1 className="Patent-head">PATENT</h1> */}
            <Tittles title={'PATENT'} span={'PATENT'}/>
            <p className="Patent-text">Dr. Sangeeta Joshi along with colleagues Sangita Kurundkar,Laxman Waghmare
             registered a patent on their name on the topic of <span>Method For Reducing Energy Need of The AD-HOC Wireless Network. </span>
            The title of Invention was "AD-HOC Wireless Network and A Method For Reducing Energy Need Of The ADHOC Wireless Network".
            To read more details about the patent 
            ...<a href={pdf} target="_blank">click here</a>
            </p>
        </div>
    );
}

export default Patent;