import React from "react";
import Event from "./Event";
import Title from "./Tittles";
import pdf1 from '../Conference/conf1.pdf';
import pdf2 from '../Conference/conf2.pdf';
import pdf3 from '../Conference/conf3.pdf';
import pdf4 from '../Conference/conf4.pdf';
import pdf5 from '../Conference/conf5.pdf';
import search1 from '../img/conference.svg';

const divStyle = {
    marginLeft: '10rem',
};
function conference(){

    var cetTitle=[
        "Real-time Systems", "ETCST-2014","ICCASP-2018", "NCMOC-2015","Technology of Education"
    ];

    var certText=[
        "National Conference on Real-Time Systems held between 1-3 November 1990 at S.G.S.I.T.S. Indore. ",
        "International Conference on Emerging Trends and Challenges in Science & Technology at Bangkok-10500, Thailand. ",
        "3rd International Conference on Computing, Communication and Signal Processing (ICCASP-2018) during 26-27 January 2018. ",
        "National Conference on Modeling Optimization and Control(NCMOC-2015) organized by BRACT's VIT, Pune. ",
        "8th IEEE International Conference on Technology of Education held between 2-4 December 2016 at IIT Bombay, Mumbai. "
    ];

    return(
        <div className="conference" style={{marginTop:"3rem", marginBottom:"2rem"}}>
            <Title style={divStyle} title={'Conference'} span={'Meets'}/>
            <div className="Event-container">
                <Event image={search1} event= {certText[0]} title={cetTitle[0]} pdf={pdf1}/>
                <Event image={search1} event= {certText[1]} title={cetTitle[1]} pdf={pdf2}/>
                <Event image={search1} event= {certText[2]} title={cetTitle[2]} pdf={pdf3}/>
                <Event image={search1} event= {certText[3]} title={cetTitle[3]} pdf={pdf4}/>
                <Event image={search1} event= {certText[4]} title={cetTitle[4]} pdf={pdf5}/>
            </div>  
        </div>
    );
}

export default conference;