import React from 'react';
function Award({image, title,AwardText,pdf}){
    return(
        <div className="Award">
            <div className="Award-content">
                    <img src={image} alt="Award"/>
                    <h5 className="Award-title">{title}</h5>
                    <p className="Award-text">
                        {AwardText}
                        <a href={pdf} target="_blank">View Certificate</a>
                    </p>
            </div>
        </div>
    );
}

export default Award;