import React from 'react';

function Event({image, event, title, pdf}){

    return(
        <div className="Event">
                <div className="Event-content">
                    <img src={image} alt="Award"/>
                    <h5 className="Event-title">{title}</h5>
                    <p className="Event-text">
                        {event}
                        <a href={pdf} target="_blank">View More</a>
                    </p>
                </div>
        </div>
    );
}

export default Event;