import React from 'react';
import Tittles from '../Components/Tittles';
import Award from './Award';
import pdf1 from '../awards/award1.pdf';
import pdf2 from '../awards/award2.pdf';
import pdf3 from '../awards/award3.pdf';
import pdf4 from '../awards/award4.pdf';
import pdf5 from '../awards/award5.pdf';
import trophy from '../img/trophy.svg';
import teacher from '../img/teacher.svg';
import conference from '../img/conference.svg';


var awardArr=[
   "Certificate of participating and presenting a paper at National Conference on TEQIP II: Best Policies and Practices ",
   "Award for Best Teacher in field of Electronics Engineering ",
   "Certificate of participating and publishing a peper on Performance Evolution of Low Leakage Tri-Gate MOSFET structure with LaOI3 High-K and TiN Metal Gate and Analysis of Temperature Effects at Ultra Short Gate Length 15nm. ",
   "Certificate of Effective Research Methodology for Ph.D. aspirants of Engineering and Technology ",
   "Worked on topic Study of Neutron under the guidance of Prof. A.N. Chandorkar as a QIP Research Scholar during the period of July 1997 to January 2001. "
];

var awards=["TEQIP Best Policies & Practices","Best Teacher Award", "IEEE Conference","Effective Research Methodology", "QIP Research Scholar"]

function Awards(){
    return(
        <div className="Awards">
            <Tittles title={'Awards'} span={'Awards'}/>
            
            <div className="Award-container">
                <Award image={trophy} title={awards[0]} AwardText={awardArr[0]} pdf={pdf1} />
                <Award image={teacher} title={awards[1]} AwardText={awardArr[1]} pdf={pdf2}/>
                <Award image={conference} title={awards[2]} AwardText={awardArr[2]} pdf={pdf3}/>
                <Award image={trophy} title={awards[3]} AwardText={awardArr[3]} pdf={pdf4}/>
                <Award image={trophy} title={awards[4]} AwardText={awardArr[4]} pdf={pdf5}/>
            </div>
            
        </div>
    );
}

export default Awards;