import React from 'react';
import Event from '../Components/Event';
import Title from "../Components/Tittles";
import pdf1 from '../pdf/event1.pdf';
import pdf2 from '../pdf/event2.pdf';
import pdf3 from '../pdf/event3.pdf';
import pdf4 from '../pdf/event4.pdf';
import search1 from '../img/search.svg';
import speaker from '../img/speaker.svg';
import code1 from '../img/code1.svg';
import code2 from '../img/code2.svg';



function Events(){

    var event1=["Short term training program in Collaboration with University of Mumbai on Effective Research Methodology for Ph.D. aspirants of Engineering. ",
    "Third International Conference on Recent Advances and Challenges in Engineering & Management on December 22-23, 2016. ",
    "Faculty Developement Program on Cadence Demo Tool Training Program at Vidyalankar Institute of Technology between June 22-22, 2017. ",
    "Faculty Developement Program on Cadence Advanced Tools at Vidyalankar Institute of Technology on 13th and 14th November 2019. ",
    "One day National workshop on NAAC Peer Team Visit Preperation: Criteria Exhibition on 12 and 13 December 2019. "];

    var title1= ["EFFECTIVE RESEARCH METHODOLOGY","RACEM 2016","Event3","CADENCE TOOLS WORKSHOP","NAAC VISIT"];
    

    return(
        <div className="Events">
            <Title title={'Events Organized'} span={'Events'}/>
            <p className="Event-Text">
            I got opportunity to organize so many events like faculty developement program,
            Training program on Effective Research Methodology, NAAC Peer Team Visit preparation.
            </p>
            <div className="Event-container">
                <Event image={search1} event= {event1[0]} title={title1[0]} pdf={pdf3}/>{/* event3 */}
                <Event image={speaker} event= {event1[1]} title={title1[1]} pdf={pdf1}/>{/* event1 */}
                <Event image={code1} event= {event1[2]} title={title1[2]} pdf={pdf2}/>{/* event2 */}
                <Event image={code2} event= {event1[3]} title={title1[3]} pdf={pdf2}/>{/* event2 */}
                <Event image={search1} event= {event1[4]} title={title1[4]} pdf={pdf4}/>{/* event4 */}
            </div>

            {/* <Conference /> */}
        </div>
    );
}

export default Events;