import React from 'react';
import avatar from '../img/sangeeta.JPG';
import {NavLink} from 'react-router-dom';

function HomePage(){
    return(
        <div className="NavBar">
            

            {/* Creating NavBar */}
            <nav className="nav">
                <div className="profile">
                    <img src={avatar} alt="Sangeeta Joshi"></img>
                </div>
                <ul className="nav-items">
                    <li className="nav-item">
                        <NavLink to="/" exact activeClassName="active">
                            Home
                        </NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink to="/about" exact activeClassName="active">
                            About
                        </NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink to="/Achievements" exact activeClassName="active">
                            Achievements
                        </NavLink>
                    </li>

                    <li className="nav-item">
                        <NavLink to="/Event" exact activeClassName="active">
                            Events
                        </NavLink>
                    </li>
                    
                    <li className="nav-item">
                        <NavLink to="/Conference" exact activeClassName="active">
                            Conference
                        </NavLink>
                    </li>

                    
                    <li className="nav-item">
                        <NavLink to="/Contact" exact activeClassName="active">
                            Contact
                        </NavLink>
                    </li>
                    
                </ul>

                <footer className="footer">
                    <p>@2021 Sangeeta Joshi</p>
                </footer>
            </nav>
        </div>
    );
}

export default HomePage;