import React from 'react';
import Title from "../Components/Tittles";
import ContactItem from "../Components/ContactItem";
import phone from "../img/call.svg";
import mail from "../img/mail.svg";
import address from "../img/address.svg";

function ContactPage(){
    return(
        <div>
            <div className="title">
                <Title title={'Contact Me'} span={'Connect'} />
            </div>
            <div className="ContactPage">
                <div className="map-sect">
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3771.9661330136287!2d72.86828231421192!3d19.021213958636736!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3be7cf394b345bdd%3A0x8d44ea4f85af3ceb!2sVidyalankar%20School%20of%20Information%20Technology!5e0!3m2!1sen!2sin!4v1617777657204!5m2!1sen!2sin" width="600" height="450" style={{border:'0'}} allowfullscreen="" loading="lazy"></iframe>
                </div>
                <div className="contact-sect">
                    <ContactItem icon={phone} text1={'+91 9896036269'} text2={''} title={'Phone'}/>
                    <ContactItem icon={mail} text1={''} text2={'sangeeta.joshi@vit.edu.in'} title={'Email'}/>
                    <ContactItem icon={address} text1={'Vidyalankar Institute of Technology'} text2={'Wadala East, Mumbai-400037'} title={'Address'}/>
                </div>
            </div>
        </div>
    );
}

export default ContactPage;